import React, { useEffect, useState, useCallback } from 'react';
import PocketBase from 'pocketbase';
import Column from './Column.jsx'
import createRandomName from './RandomNameGenerator.jsx'

const Overview = ({ setAuthenticated }) => {
    const client = new PocketBase('http://127.0.0.1:8090');

    const [boards, setBoards] = useState([]);
    const [board, setBoard] = useState([]);
    const [columns, setColumns] = useState([]);
    const [tickets, setTickets] = useState([]);
    const [drag, setDrag] = useState();

    const addColumnCB = useCallback((e) => {
        setColumns(c => [...c, e]);
    }, []);

    const removeColumnCB = useCallback((e) => {
        setColumns(t => t.filter(t => t.id !== e.record.id));
    }, []);

    const updateColumnCB = useCallback((e) => {
        setColumns(t => t.map(c => {
            if (c.id === e.id) {
                return e;
            }
            else return c;
        }));
    }, []);

    // realtime
    async function SubscribeToColumn() {
        console.log("subscribing to columns");
        console.log(client);
        client.collection("column").subscribe("*", e => {
            console.log("Event C", e.action, e.record);
            if (e.action === "update") {
                updateColumnCB(e.record)
            } else if (e.action === "create") {
                addColumnCB(e.record);
            }
            else if (e.action === "delete") {
                removeColumnCB(e);
            }
        });
        return () => {
            client.collection("column").unsubscribe();
        };
    }

    const addTicketCB = useCallback((e) => {
        setTickets(ts => [...ts, e.record]);
    }, []);

    const removeTicketCB = useCallback((e) => {
        setTickets(t => t.filter(t => t.id !== e.record.id));
    }, []);

    const updateTicketsCB = useCallback((e) => {
        setTickets(t => t.map(t => {
            if (t.id === e.record.id) {
                return e.record;
            }
            else return t;
        }))
    }, []);


    // realtime
    async function SubscribeToTickets() {
        console.log("subscribing to tickets");
        client.collection("ticket").subscribe("*", function (e) {
            console.log("Event T", e.action, e.record);
            if (e.action === "update") {
                updateTicketsCB(e);
            } else if (e.action === "create") {
                addTicketCB(e);
            } else if (e.action === "delete") {
                removeTicketCB(e);
            }
        });
        return () => {
            client.collection("column").unsubscribe();
        };
    }

    const getBoards = async () => {
        try {
            const boards = await client.collection("board").getFullList();
            setBoards(boards);
            if (boards) { // Load first board aswell
                getBoard(boards[0].id);
            }
        } catch (error) {
            console.log('Error occurred while fetching boards', error);
        }
    };


    const getBoard = async (boardName) => {
        try {
            const board = await client.collection("board").getOne(boardName);
            setBoard(board)
            getColumns(board)
            getTickets(board.id)
        } catch (error) {
            console.log('Error occurred while fetching boards', error);
        }
    };

    async function getColumns(board) {
        try {
            const columns = await client.collection("column").getFullList({ filter: `board = '${board.id}'` });
            setColumns(columns);
        } catch (error) {
            console.log('Error occurred while fetching boards', error);
        }
    };

    async function getTickets(board) {
        console.log("GT:", board, tickets);
        try {
            const retTickets = await client.collection("ticket").getFullList({ filter: `board = '${board}'` });
            setTickets(retTickets);
        } catch (error) {
            console.log('Error occurred while fetching boards', error);
        }
    };

    async function dropDrag(column) {
        console.log("Moving...");
        const cFromId = drag.fromColumnId;
        const cToId = column.id;
        const ticketId = drag.id;
        try {
            // Remove from current
            const colFrom = await client.collection("column").getOne(cFromId);
            colFrom.tickets = colFrom.tickets.filter(t => t !== ticketId);

            // add to drop
            const colTo = await client.collection("column").getOne(cToId);
            colTo.tickets.push(ticketId);

            console.log("---", cFromId, cToId, colFrom.tickets, colTo.tickets);
            // Execute 
            await client.collection("column").update(cToId, colTo);
            await client.collection("column").update(cFromId, colFrom);

        } catch (error) {
            console.log('Error', error);
        }


        await removeTicketFromColumn(column.id, drag.id)
        addTicketToColumn(column.id, drag.id);
    }

    async function createColumn(board) {
        try {
            console.log("Board - col", board);
            const data = new FormData();
            data.set('title', 'New col');
            data.set('board', board.id);
            data.set('tickets', []);
            await client.collection("column").create(data);
        } catch (error) {
            console.log('Error', error);
        }
    };

    async function updateTicket(ticket) {
        try {
            console.log("Update ticket:", ticket);
            client.collection("ticket").update(ticket.id, ticket);
        } catch (error) {
            console.log('Error', error);
        }
    };

    async function updateColumn(column) {
        try {
            console.log("Update column:", column);
            client.collection("column").update(column.id, column);
        } catch (error) {
            console.log('Error', error);
        }
    };

    async function createNewBoard() {
        try {
            console.log("create board:");
            const data = new FormData();
            data.set('name', createRandomName());
            data.set('owner', client.authStore.model.id);
            client.collection("board").create(data);
        } catch (error) {
            console.log('Error', error);
        }
    };

    async function createTicket(columnId, board) {
        try {
            const data = new FormData();
            data.set('title', createRandomName());
            data.set('board', board);
            data.set('column', columnId);
            const newCard = await client.collection("ticket").create(data);
            addTicketToColumn(columnId, newCard.id)
        } catch (error) {
            console.log('Error', error);
        }
    };

    async function addTicketToColumn(columnId, cardId) {
        try {
            const col = await client.collection("column").getOne(columnId);
            col.tickets.push(cardId);
            client.collection("column").update(columnId, col);
        } catch (error) {
            console.log('Error', error);
        }
    };

    async function removeTicketFromColumn(columnId, ticketId) {
        try {
            const col = await client.collection("column").getOne(columnId);
            const index = columns.map(c => c.id).indexOf(ticketId);
            if (index > -1) { // only splice array when item is found
                col.tickets.splice(index, 1); // 2nd parameter means remove one item only
            }
            await client.collection("column").update(columnId, col);
        } catch (error) {
            console.log('Error', error);
        }
    };


    async function removeColumn(cId) {
        try {
            await client.collection("column").delete(cId);
        } catch (error) {
            console.log('Error', error);
        }
    };

    async function removeTicket(tId) {
        try {
            await client.collection("ticket").delete(tId);
        } catch (error) {
            console.log('Error', error);
        }
    };

    useEffect(() => {
        console.log("Starting Ticketor...");
        getBoards();
        SubscribeToColumn();
        SubscribeToTickets();
    }, []);

    return (
        < div >
            <NavBar setAuthenticated={setAuthenticated} client={client} createNewBoard={createNewBoard} boards={boards} getBoard={getBoard} />
            <Board
                columns={columns}
                createTicket={createTicket}
                createColumn={createColumn}
                updateColumn={updateColumn}
                removeColumn={removeColumn}
                removeTicket={removeTicket}
                updateTicket={updateTicket}
                createNewBoard={createNewBoard}
                tickets={tickets}
                setDrag={setDrag}
                dropDrag={dropDrag}
                board={board}
            />
        </div >
    );
};


const NavBar = ({ setAuthenticated, client, boards, getBoard, createNewBoard }) => {
    return (
        <div className="flex flex-row justify-between ml-5 mr-5">
            <ul className="flex flex-row">
                <li className="mr-6">
                    <a className="text-blue-500 hover:text-blue-800" href="/">Home</a>
                </li>
                <li className="mr-6 group relative dropdown">
                    <p className="text-blue-500 hover:text-blue-800">Boards</p>
                    <div className='group-hover:block dropdown-menu absolute hidden h-auto '>
                        <ul className="top-0 w-48 bg-white shadow px-3 py-3">
                            {Array.from(boards).map(b =>
                                <li className='hover:bg-slate-200 cursor-pointer p-1'
                                    onClick={() => getBoard(b.id)}
                                    key={b.id}>
                                    {b.name}
                                </li>
                            )}
                            <a className='hover:bg-slate-200 cursor-pointer p-1'
                                onClick={() => createNewBoard()}
                                key={"new"}
                                href="/">
                                <i>{"New board..."}</i>
                            </a>
                        </ul>
                    </div>
                </li>
                <li className="mr-6">
                    <button className="text-blue-500 cursor-pointer hover:text-blue-800"
                        onClick={() => { console.log(setAuthenticated); setAuthenticated(false); client.authStore.clear() }} >Logout
                    </button>
                </li>
            </ul >
            <ul className="flex flex-row">
                <li className='cursor-default'>
                    <p onClick={() => console.log(client.authStore.model)}>{client.authStore.model.name}</p>
                    <p className='text-xs text-gray-500'>{client.authStore.model.id}</p>
                    <p className='text-xs text-gray-500'>{client.authStore.model.email}</p>
                    <p className='text-xs text-gray-500'>{client.authStore.model.username}</p>
                </li>
            </ul >
        </div>
    )
}

const Board = ({ board, columns, tickets, createNewBoard, createTicket, removeTicket, updateTicket, createColumn, removeColumn, updateColumn, setDrag, dropDrag }) => {
    return (
        <div className='m-5 overflow-auto'>
            <h1 className='text-xl text-bold cursor-default ml-1'>{board.name}</h1>
            <div className="flex flex-row">
                {
                    Array.from(columns).map((c =>
                        <Column
                            column={c}
                            key={c.id}
                            tickets={tickets}
                            createTicket={createTicket}
                            removeTicket={removeTicket}
                            removeColumn={removeColumn}
                            updateTicket={updateTicket}
                            updateColumn={updateColumn}
                            setDrag={setDrag}
                            dropDrag={dropDrag}
                        />
                    ))
                }
                {board.name ?
                    <button
                        onClick={() => createColumn(board)}
                        className='text-xs text-gray-400 bold'>+ Add another lane
                    </button>
                    :
                    <a href="/"
                        onClick={() => createNewBoard()}
                        className='text-xs text-gray-400 bold'>Create a board...
                    </a>
                }

            </div>
        </div>
    );
};

export default Overview;