import React, { } from 'react';

const Ticket = ({ ticket, updateTicket, removeTicket, setDrag, column }) => {
    return (
        <div
            draggable
            onDragStart={() => { ticket.fromColumnId = column.id; setDrag(ticket) }}
            className="block m-2 max-w-sm p-6 bg-white border border-gray-200 rounded-lg shadow hover:bg-gray-100 dark:bg-gray-800 dark:border-gray-700 dark:hover:bg-gray-700"
            onClick={() => console.log(ticket)} >
            <div className='flex flex-row items-end justify-between'>
                <i className='text-xs text-gray-400 italic md:not-italic' >{ticket.id}</i>
                <img
                    src="data:image/png;base64,"
                    alt='thrash-card'
                    className='cursor-pointer opacity-20 hover:opacity-70'
                    onClick={() => removeTicket(ticket.id)}
                />
            </div>
            <input type="text"
                onChange={e => { ticket.title = e.target.value; }}
                className='placeholder-gray-800 border-red-400'
                placeholder={ticket.title}>
            </input>
            <input type="text"
                onChange={e => { ticket.description = e.target.value; }}
                className='placeholder-gray-800 border-red-400 text-xs'
                placeholder={ticket.description}>
            </input>
            <br />
            <br />
            <div className='flex flex-row items-end justify-between'>
                <h1></h1>
                <div className='text-end'>
                    <p className='text-xs text-gray-400'>created {ticket.created.substr(0, 19)}</p>
                    <p className='text-xs text-gray-400'>updated {ticket.updated.substr(0, 19)}</p>
                </div>

            </div>
            <img src="data:image/png;base64,"
                alt='save-card'
                className='cursor-pointer opacity-10 hover:opacity-70'
                onClick={() => updateTicket(ticket)}
            />
        </div >
    );
};

export default Ticket;
