import './index.css';
import Overview from './Board.jsx'
import Login from './Login.jsx'
import React, { useState, useEffect } from 'react';

import PocketBase from 'pocketbase';


function App() {
  const client = new PocketBase('http://127.0.0.1:8090');
  const [authhenticated, setAuthenticated] = useState(false);


  useEffect(() => {
    setAuthenticated(client.authStore.isValid);
  }, []);

  return (
    <div className="App">
      {authhenticated ?
        <Overview setAuthenticated={setAuthenticated} />
        :
        <Login setAuthenticated={setAuthenticated} client={client} />
      }
    </div>
  );
}

export default App;
