import React, { } from 'react';
import Ticket from './Ticket.jsx'

const Column = ({ column, tickets, removeColumn, updateColumn, createTicket, removeTicket, updateTicket, setDrag, dropDrag }) => {
    return (
        <div
            onDragOver={e => e.preventDefault()}
            onDrop={() => dropDrag(column)}
            className="flex-col bg-slate-50 w-full m-1 w-full max-w-sm bg-white rounded-lg shadow dark:bg-gray-800 dark:border-gray-700">
            <input type="text"
                onChange={e => { column.title = e.target.value; }}
                className='placeholder-gray-800 border-red-400 m-5'
                placeholder={column.title}>
            </input>
            <div className='flex flex-row justify-between'>
                <img
                    src="data:image/png;base64,"
                    alt='thrash-card'
                    className='cursor-pointer opacity-20 hover:opacity-70 m-1'
                    onClick={() => removeColumn(column.id)}
                />
                <img src="data:image/png;base64,"
                    alt='save-card'
                    className={'cursor-pointer opacity-20 hover:opacity-70 m-1'}
                    onClick={() => updateColumn(column)}
                />
            </div>
            {Array.from(tickets.values())
                .filter(t => column.tickets.includes(t.id))
                .map(t =>
                    <Ticket
                        ticket={t}
                        key={t.id}
                        removeTicket={removeTicket}
                        updateTicket={updateTicket}
                        setDrag={setDrag}
                        column={column}
                    />
                )
            }
            <button
                onClick={() => createTicket(column.id, column.board)}
                className='text-xs text-gray-400 bold'>+ Add another card</button>

        </div >
    );
};

export default Column;
